:root {
  --bg-preloader: linear-gradient(to bottom, rgb(160, 213, 246) 2%,rgba(229,235,238,1) 50%,rgba(0,0,0,1) 100%,rgba(0,0,0,1) 100%,rgba(245,247,249,1) 100%,rgba(0, 0, 0, 0.59) 100%);
  --preloader-bg: linear-gradient(to bottom, rgb(160, 213, 246) 2%,rgba(229,235,238,1) 50%,rgba(0,0,0,1) 100%,rgba(0,0,0,1) 100%,rgba(245,247,249,1) 100%,rgba(0, 0, 0, 0.59) 100%);
}

.header {
  grid-column: 1/3;
  grid-row: 1/2;
  background-color: rgb(107, 186, 255);
  box-shadow: inset 0 0 30px rgb(205, 232, 255);
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
}
.header__logo {
  height: 80%;
  margin-top: 8px;
}
.header__logout {
  margin-left: 15px;
  width: 30px;
  height: 30px;
  background: transparent;
}
.header__logout img {
  width: 100%;
  height: 100%;
}
.header__login {
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 1px;
  color: gold;
  text-shadow: 0 0 8px rgb(54, 54, 206);
}
.header__login_link {
  font-size: 20px;
  font-weight: 600;
  text-shadow: 0 0 3px gold;
  letter-spacing: 1px;
  color: blue;
}
.header__login_link:focus {
  color: gold;
  text-shadow: 0 0 8px rgb(54, 54, 206);
}
.header__login_email {
  font-size: 14px;
  color: black;
  text-shadow: none;
  padding-right: 15px;
}

.main {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  grid-column: 2/3;
  grid-row: 2/3;
  box-shadow: -2px -2px 15px black;
  padding: 20px;
  min-height: 100%;
  background: linear-gradient(to bottom, rgb(160, 213, 246) 2%, rgb(229, 235, 238) 50%, rgb(0, 0, 0) 100%, rgb(0, 0, 0) 100%, rgb(245, 247, 249) 100%, rgba(0, 0, 0, 0.59) 100%);
}
.main .info {
  display: flex;
  position: relative;
}
.main .info__img-box {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 0 15px;
}
.main .info__img {
  width: 100%;
  height: 100%;
}
.main .info__list {
  display: flex;
  flex-direction: column;
  font-size: 17px;
  justify-content: space-between;
  margin-left: 20px;
}
.main .info__name {
  margin-bottom: 10px;
  font-size: 19px;
  font-weight: 700;
}

.status-form {
  position: absolute;
  top: 0;
  right: 0;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}
.status-form__inactive {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 170px;
  background: rgb(187, 187, 187);
  padding: 5px;
}
.status-form__active_input {
  max-width: 360px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 5px;
}

.form {
  margin-top: 20px;
}
.form p {
  font-size: 29px;
  margin-bottom: 10px;
}
.form__input {
  background-color: rgba(169, 169, 169, 0.58);
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  font-size: 23px;
  resize: none;
  padding: 10px;
  border-radius: 3px;
}
.form__button {
  background: rgba(109, 153, 51, 0.295);
  font-size: 18px;
  color: black;
  margin-top: 10px;
  border-radius: 3px;
  padding: 10px;
  margin-left: 100%;
  transform: translateX(-100%);
}

.post-form {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 77px;
  grid-template-rows: repeat(2);
}
.post-form__textarea-cntr-error {
  grid-column: 1/3;
}
.post-form__textarea {
  background-color: rgba(169, 169, 169, 0.58);
  width: 100%;
  height: 100px;
  font-size: 20px;
  resize: none;
  padding: 10px;
  border-radius: 3px;
  white-space: normal;
}
.post-form__submit-cntr {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  grid-column: 2/3;
  grid-row: 2/3;
}
.post-form__submit-button {
  background: rgba(109, 153, 51, 0.295);
  font-size: 18px;
  color: black;
  margin-top: 10px;
  border-radius: 3px;
  padding: 5px 10px;
  cursor: pointer;
}

.sidebar {
  padding: 20px;
  min-height: 100%;
  background: linear-gradient(to bottom, rgb(160, 213, 246) 2%, rgb(229, 235, 238) 50%, rgb(0, 0, 0) 100%, rgb(0, 0, 0) 100%, rgb(245, 247, 249) 100%, rgba(0, 0, 0, 0.59) 100%);
  grid-column: 1/2;
  grid-row: 2/3;
}
.sidebar__list {
  font-size: 20px;
  font-weight: 600;
  display: grid;
  gap: 10px;
}
.sidebar__item {
  transition: 0.05s linear;
  text-transform: uppercase;
}
.sidebar__item a {
  color: gold;
  text-shadow: 2px 2px rgb(164, 122, 42);
  position: relative;
}
.sidebar__item a.active {
  text-shadow: 4px 4px 4px rgb(34, 67, 103);
  margin-left: -2px;
  color: rgb(40, 64, 82);
}
.sidebar__item:nth-child(5) {
  margin-top: 30px;
}
.sidebar__item:last-child {
  position: absolute;
  bottom: 30px;
}
.sidebar__item a:hover {
  text-shadow: 4px 4px 4px rgb(68, 80, 93);
  margin-left: -2px;
  transition: 0.05s linear;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  cursor: default;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul, li {
  list-style: none;
}

img {
  vertical-align: top;
}

.post {
  position: relative;
  margin: 10px;
  display: flex;
  align-items: center;
  border-radius: 20px;
  background: rgb(54, 111, 185);
  margin-left: 0;
}
.post__img {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  opacity: 0.5;
  border-radius: 20px;
}
.post__text {
  font-size: 18px;
  font-weight: 550;
}
.post__like-meaning {
  margin: 0 10px 0 10px;
  font-size: 20px;
  font-weight: 700;
  color: rgb(255, 255, 255);
  right: 17px;
  position: absolute;
}
.post__like {
  margin: 0 10px 0 10px;
  font-size: 20px;
  font-weight: 700;
  color: rgb(255, 255, 255);
  right: 35px;
  bottom: -10px;
  position: absolute;
}
.post__dislike {
  position: absolute;
  transform: rotate(180deg);
  right: 0px;
  bottom: -10px;
}
.post__like-box {
  position: absolute;
  right: 10px;
  display: flex;
  align-items: center;
}
.post__button {
  background: transparent;
}
.post__button img {
  width: 20px;
  height: 20px;
}

:root {
  --dialog-bg: linear-gradient(to bottom, rgb(160, 213, 246) 2%,rgba(229,235,238,1) 50%,rgba(0,0,0,1) 100%,rgba(0,0,0,1) 100%,rgba(245,247,249,1) 100%,rgba(0, 0, 0, 0.59) 100%);
  --dialog-users-focus: linear-gradient(to bottom, rgba(30,87,153,1) 3%,rgba(30,87,153,0.95)	6%,rgba(30,87,153,0.82) 14%,rgba(30,87,153,0.41) 38%,rgba(30,87,153,0.45) 42%,rgba(32,124,202,0.53) 50%,rgba(125,185,232,0.77) 74%,rgba(125,185,232,0.83) 80%,rgba(125,185,232,0.88) 85%,rgba(125,185,232,0.95) 92%,rgba(125,185,232,1) 97%,rgba(125,185,232,1) 100%);
  --dialog-users-unfocus: linear-gradient(to bottom, rgba(125,185,232,1) 3%,rgba(30,87,153,0.65) 6%,rgba(30,87,153,0.41) 8%,rgba(30,87,153,0.5) 21%,rgba(32,124,202,0.69) 50%,rgba(125,185,232,0.97) 92%,rgba(57,115,176,1) 97%,rgba(30,87,153,1) 99%,rgba(30,87,153,1) 100%);
}

.dialogs {
  display: grid;
  grid-template-columns: 250px 1fr;
  grid-template-rows: 30px 1fr 100px;
  width: 100%;
  height: 100%;
  padding-top: 20px;
  border: 2px solid black;
  box-shadow: -2px -2px 15px black;
  background: var(--dialog-bg);
  height: 100%;
}
.dialogs__title {
  grid-column: 1/3;
  margin-left: 15px;
}
.dialogs__name-list {
  border-radius: 10px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 7px;
  transition: 0.5s linear;
  grid-column: 1/2;
}
.dialogs__name-item {
  width: 100%;
  font-size: 18px;
  font-weight: 600;
  border: 1px solid rgb(63, 63, 63);
  border-radius: 4px;
  padding: 8px;
  cursor: pointer;
  background: var(--dialog-users-unfocus);
  color: #000;
}
.dialogs__chat-container {
  overflow: auto;
  height: 100%;
  background: rgba(120, 120, 120, 0.2);
  border-top-left-radius: 10px;
  padding: 15px;
}
.dialogs__chat-container::-webkit-scrollbar {
  display: none;
}
.dialogs__chat {
  min-height: 100%;
  gap: 20px;
  padding-right: 10px;
  grid-column: 2/3;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.dialogs .user-name {
  width: 300px;
  height: 50px;
  background: rgba(69, 0, 0, 0.33);
  border-radius: 30px;
}
.dialogs__message {
  display: flex;
  border-radius: 10px;
  align-items: center;
}
.dialogs__ava-who {
  display: grid;
  justify-items: center;
}
.dialogs__ava {
  margin-bottom: 5px;
  width: 60px;
  height: 60px;
  border-radius: 30px;
}
.dialogs__who {
  font-size: 18px;
  font-weight: 600;
}
.dialogs__dialog-box {
  padding: 13px;
  font-size: 16px;
  font-weight: 600;
  border: 1px solid;
  margin-left: 35px;
  box-shadow: inset 0 0 10px rgb(30, 87, 153);
  border-radius: 10px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.dialogs__form {
  grid-column: 2/3;
  display: flex;
  margin: 10px;
  margin-left: 0;
  align-items: center;
}
.dialogs__area {
  resize: none;
  width: 100%;
  border-radius: 3px;
  background-color: rgba(169, 169, 169, 0.58);
  font-size: 18px;
  padding: 10px;
  height: 100%;
}
.dialogs__area::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.781);
}
.dialogs__area::-webkit-scrollbar {
  display: none;
}
.dialogs__button {
  height: 25px;
  padding: 0 15px;
  background: rgba(186, 247, 105, 0.534);
  font-size: 18px;
  font-weight: 600;
  color: black;
  border-radius: 3px;
  margin-left: 10px;
}

.news {
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgb(160, 213, 246) 2%, rgb(229, 235, 238) 50%, rgb(0, 0, 0) 100%, rgb(0, 0, 0) 100%, rgb(245, 247, 249) 100%, rgba(0, 0, 0, 0.59) 100%);
}

.music {
  width: 100%;
