.playlist {
  padding: 10px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  box-shadow: inset 0 0 20px rgb(83, 83, 83), 0 0 15px rgb(83, 83, 83);
}
.playlist__item {
  width: 100%;
  height: 50px;
  display: flex;
  background: rgb(83, 83, 83);
  border-radius: 7px;
  border: 1px solid rgb(39, 39, 39);
  overflow: hidden;
