:root {
  --bg: linear-gradient(to bottom, rgb(160, 213, 246) 2%,rgba(229,235,238,1) 50%,rgba(0,0,0,1) 100%,rgba(0,0,0,1) 100%,rgba(245,247,249,1) 100%,rgba(0, 0, 0, 0.59) 100%);
  --preloader-bg: linear-gradient(to bottom, rgb(160, 213, 246) 2%,rgba(229,235,238,1) 50%,rgba(0,0,0,1) 100%,rgba(0,0,0,1) 100%,rgba(245,247,249,1) 100%,rgba(0, 0, 0, 0.59) 100%);
}

.users {
  background: var(--bg);
  height: 100%;
  width: 100%;
  padding: 30px;
  padding-top: 10px;
  border: 2px solid black;
  box-shadow: -2px -2px 15px black;
  overflow-x: hidden;
}
.users__title {
  text-align: center;
  font-size: 27px;
  font-weight: 800;
  color: gold;
  text-shadow: 2px 2px rgb(164, 122, 42);
}
.users__page-number {
  width: 100%;
  overflow-y: scroll;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  flex-wrap: wrap;
  box-shadow: inset 0 0 15px, 0 0 15px;
  margin: 20px 0 25px;
}
.users__page-number::-webkit-scrollbar {
  display: none;
}
.users__page-number-button {
  width: 25px;
  height: 20px;
  padding: 0 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 5px;
  border: 1.5px solid rgb(18, 108, 182);
  box-shadow: inset 0 0 12px rgb(223, 208, 0);
}
.users__page-number-button-disable {
  cursor: default;
  border: 1.5px solid rgb(24, 24, 24);
  box-shadow: inset 0 0 12px rgb(68, 68, 68);
}
.users__container {
  margin: 10px;
  gap: 15px;
  display: grid;
}
.users__user {
  display: grid;
  grid-template-columns: 15% 85%;
  height: 80px;
  width: 100%;
}
.users__user-foto {
  width: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.users__foto-box {
  height: 50px;
}
.users__foto {
  height: 100%;
  border-radius: 50%;
  box-shadow: 0 0 10px black;
}
.users__button_follow {
  padding: 5px 10px;
  border-radius: 5px;
  border: 1.5px solid black;
  font-weight: 600;
  background: rgb(241, 211, 76);
}
.users__button_unfollow {
  padding: 5px 10px;
  border-radius: 5px;
  border: 1.5px solid black;
  font-weight: 600;
  background: rgb(205, 100, 90);
  color: white;
}
.users__button_show-more {
  height: 40px;
  width: 60%;
  display: block;
  margin: 0 auto;
  border-radius: 20px;
  font-size: 20px;
  box-shadow: inset 0 0 10px black;
}
.users__user-info {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 15px;
  border: 2px solid black;
  border-radius: 15px;
  margin-left: 10px;
}
.users__name {
  font-size: 19px;
  font-weight: 600;
}
.users__id {
  font-size: 16px;
  font-weight: 600;
}

.login {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: rgb(37, 37, 37);
}
.login__title {
  position: relative;
  top: -15%;
  color: rgb(141, 141, 141);
  font-size: 27px;
}
.login__form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.login__global-error {
  color: red;
  font-size: 16px;
}
.login__email-cntr {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 5px;
  padding: 10px;
  margin: 0 auto;
  position: relative;
  border: 1px solid rgb(141, 141, 141);
}
.login__email-input {
  border-radius: 3px;
  padding: 3px;
  padding-left: 6px;
}
.login__error-email {
  position: absolute;
  top: -7px;
  font-size: 11px;
  background: rgb(37, 37, 37);
  padding: 0 5px;
  color: red;
}
.login__password-cntr {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 5px;
  padding: 10px;
  margin: 0 auto;
  position: relative;
  border: 1px solid rgb(141, 141, 141);
}
.login__password-input {
  border-radius: 3px;
  padding: 3px;
  padding-left: 6px;
}
.login__error-pass {
  position: absolute;
  top: -7px;
  font-size: 11px;
  background: rgb(37, 37, 37);
  padding: 0 5px;
  color: red;
}
.login__checkbox {
  display: flex;
  align-items: center;
  color: rgb(141, 141, 141);
  margin: 0 auto;
}
.login__checkbox-input {
  margin-left: 5px;
  margin-bottom: -3px;
  display: block;
}
.login__submit {
  border-radius: 5px;
  padding: 3px 5px;
  font-weight: 600;
  margin: 0 auto;
  display: block;
  cursor: pointer;
}

label {
  display: block;
}

#root {
  width: 100%;
  background: rgb(70, 70, 70);
  height: 100%;
}

.container {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  width: 80%;
  height: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 20% 80%;
  grid-template-rows: 10vh 90vh;
  box-shadow: 0 0 20px black;
}

.huein {
  grid-row: 2/3;
  grid-column: 2/3;
  height: 100%;
  width: 100%;
  position: relative;
}

.preloader {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: var(--bg-preloader);
  box-shadow: inset 0 0 30px rgb(205, 232, 255);
}